.trim {
  max-height: 60%;
  max-width: 80%;
  margin-left: 10%;
  overflow: hidden;
}

@media screen and (max-width: 750px){
  .hide-card {
    display: none;
  }
  .fix-carousel {
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
  }
  .fix-image {
    width: 500%
  }
  .phone-trending{
    width: 70vw
  }
}
