.stars{
    font-size: 1.5rem;
    font-style: normal;
    position: relative;
    top: -.1rem;
}

.stars i {
    font-style: normal;
}

.full-star:after {
    content: "\2605";
}

.empty-star:after {
    content: "\2606";
}
.recipe-pic {
    height: 60vh;
    width: auto;

}

ul.ingredients {
    list-style-type: circle;
  }

@media only screen and (max-width: 500px) {
    .ratings {
      -webkit-flex-direction: column;
              flex-direction: column;
    }
}
